<template>
  <div class="swap">
    <el-tabs v-model="activeName" class="swap-tabs">
      <el-tab-pane label="Swap" name="Swap">
        <div class="swap-info PB-20">
          <div class="swap-top">
            <img src="@/assets/images/trend.png" class="swap-top__icon"/>
            <div class="swap-info__title MB-10">Swap</div>
            <div class="swap-info__title-info">Trade tokens in an instant</div>
          </div>
          <div class="swap-item">
            <div class="swap-item__top flex-row-between">
              <div class="flex-row-start align-center pointer">
                <div class="flex-row-start align-center" @click="openTokensDialog('item1')">
                  <img v-lazy="'https://assets-cdn.trustwallet.com/blockchains/smartchain/assets/'+item1.address+'/logo.png'" class="swap-item__top-icon MR-10"/>
                  <div class="swap-item__top-name text-overflow__1">{{item1.name}}</div>
                  
                  <el-icon class="ML-5">
                    <ArrowDown />
                  </el-icon>
                </div>
                <el-icon class="ML-5" @click="copyHandler(item1.address)"><CopyDocument /></el-icon>
              </div>
              <div v-if="isLogin">
                <el-icon class="is-loading" v-if="item1.loading">
                  <Loading />
                </el-icon>
                <template v-else-if="item1.balance">
                  Balance：{{getBit(utils.formatEther(item1.balance), 5) }}
                </template>
              </div>
            </div>
            <div class="swap-item__input MT-10 flex-row-start align-center" :class="{'is-disabled': !isLogin}">
              <input :disabled="!isLogin || approveLoading" class="text-right flex-1" @input="price1Change('item1')" v-model="item1.price" placeholder="0.00">
              <el-tag class="ML-10 ml-2 pointer" effect="light" v-if="isLogin" @click="maxHandler" type="warning" size="small">MAX</el-tag>
            </div>
          </div>
          <img src="@/assets/images/transform.png" class="swap-transform pointer" @click="transformHandler" />
          <div class="swap-item">
            <div class="swap-item__top flex-row-between">
              <div class="flex-row-start align-center pointer">
                <div class="flex-row-start align-center" @click="openTokensDialog('item2')">
                  <img v-lazy="'https://assets-cdn.trustwallet.com/blockchains/smartchain/assets/'+item2.address+'/logo.png'" class="swap-item__top-icon MR-10"/>
                  <div class="swap-item__top-name text-overflow__1">{{item2.name}}</div>
                  <el-icon class="ML-5">
                    <ArrowDown />
                  </el-icon>
                </div>
                <el-icon class="ML-5" @click="copyHandler(item2.address)"><CopyDocument /></el-icon>
                <img class="ML-5 swap-item__top-wallet" v-if="isLogin && connectState == 1" src="@/assets/images/metamask.png" @click="addTokenHandler"/>
              </div>
              <div v-if="isLogin">
                <el-icon class="is-loading" v-if="item2.loading">
                  <Loading />
                </el-icon>
                <template v-else-if="item2.balance">
                  Balance：{{getBit(utils.formatEther(item2.balance), 5)}}
                </template>
              </div>
            </div>
            <div class="swap-item__input MT-10 flex-row-start align-center" :class="{'is-disabled': !isLogin}">
              <input :disabled="!isLogin || approveLoading" v-model="item2.price" class="text-right flex-1" @input="price1Change('item2')" placeholder="0.00">
            </div>
          </div>
          <div class="swap-slippage PL-30 PR-30 MT-10">
            
            <div class="swap-slippage__select flex-row-between pointer">
              <div class="text-16">GWEI</div>
              <div class="flex-row-end">
                <el-tag class="swap-slippage__select-tag" type="warning" :effect="gWei == 5 ? 'dark' : 'light'" @click="toggleGwei(5)">5</el-tag>
                <el-tag class="swap-slippage__select-tag" type="warning" :effect="gWei == 7 ? 'dark' : 'light'" @click="toggleGwei(7)">7</el-tag>
                <el-tag class="swap-slippage__select-tag" type="warning" :effect="gWei == 10 ? 'dark' : 'light'" @click="toggleGwei(10)">10</el-tag>
                <el-tag class="swap-slippage__select-tag" type="warning" :effect="gWei == 15 ? 'dark' : 'light'" @click="toggleGwei(15)">15</el-tag>
                <el-tag class="swap-slippage__select-tag" type="warning" :effect="gWei == 20 ? 'dark' : 'light'" @click="toggleGwei(20)">20</el-tag>
                <input type="number" class="swap-slippage__select-input" placeholder="5" v-model.number="gWei">
                <!-- <el-icon class="MR-5"><CaretBottom /></el-icon> -->
              </div>
            </div>
          </div>
          <div class="PL-30 PR-30 MT-30">
            <div class="flex-row-between MB-10">
              <div class="text-yellow">Slippage Tolerance</div>
              <div class="text-blue">{{progress}}%</div>
            </div>
            <div>
              <el-slider v-model="progress" :min="0.1" :max="99" @input="progressChange"/>
              <div class="flex-row-between text-yellow">
                <div>0%</div>
                <div>100%</div>
              </div>
            </div>
          </div>
          <el-button v-if="!isLogin" class="swap-btn MT-30" @click="loginFn">Connect Wallet</el-button>
          <el-button v-else-if="approveLoading" loading class="swap-btn MT-30">Approving</el-button>
          <template v-else>
            <el-button v-if="!item1.price" disabled class="swap-btn MT-30">Enter an amount</el-button>
            <el-button v-else class="swap-btn MT-30" :loading="swapLoading" @click="swapSubmit">Swap</el-button>
          </template>
          <div class="flex-row-center">
            <el-checkbox v-model="professional" label="Professional" size="large" />
          </div>
        </div>
      </el-tab-pane>
      <el-tab-pane label="Limit" name="Limit">Limit</el-tab-pane>
      <el-tab-pane label="Liquidity" name="Liquidity">Liquidity</el-tab-pane>
      <el-tab-pane label="Perpetual" name="Perpetual">Perpetual</el-tab-pane>
    </el-tabs>
  </div>

  <Tokens ref="tokens" @change="tokenChange"/>
  <SwapConfirm ref="swapConfirm" :confirm="swapHandler" :amountOutMin="amountOutMin.toString()"/>
</template>

<script>
import Tokens from './modal/tokens.vue'
import SwapConfirm from './modal/swapConfirm.vue'
import copy from 'copy-text-to-clipboard'
import { ElMessage, ElNotification } from 'element-plus'
import { utils, BigNumber } from 'ethers'
import { getBit, Tips } from '../../libs/utils'
import config from '../../plugins/wallet/config.json'
let path = [];
let addressPath = [];
export default {
  data() {
    return {
      utils,
      getBit,
      // 是否开启专家模式
      professional: false,
      swapLoading: false,
      approveLoading: false,
      activeName: 'Swap',
      progress: 10,
      serviceCharge: 0,
      isLogin: false,
      gWei: 5,
      type: '',
      timer: null,
      // 登录类型
      connectState: '',
      item1: {
        name: "BNB",
        symbol: "BNB",
        address: "0xbb4CdB9CBd36B01bD1cBaEBF2De08d9173bc095c",
        logoURI: "https://bscscan.com/token/images/binance_32.png",
        balance: '',
        price: '0',
        loading: false
      },
      item2: {
        name: "BUSD",
        symbol: "BUSD",
        address: "0xe9e7CEA3DedcA5984780Bafc599bD69ADd087D56",
        logoURI: "https://bscscan.com/token/images/busd_32.png",
        balance: '',
        price: '0',
        loading: false
      },
      addresss: [{
        name: 'btc',
        address: '0x7130d2A12B9BCbFAe4f2634d864A1Ee1Ce3Ead9c'
      },{
        name: 'eth',
        address: '0x2170Ed0880ac9A755fd29B2688956BD959F933F8'
      },{
        name: 'bnb',
        address: '0xbb4CdB9CBd36B01bD1cBaEBF2De08d9173bc095c'
      },{
        name: 'busd',
        address: '0xe9e7CEA3DedcA5984780Bafc599bD69ADd087D56'
      },{
        name: 'usdt',
        address: '0x55d398326f99059fF775485246999027B3197955'
      }] 
    }
  },
  components: {
    Tokens,
    SwapConfirm
  },
  computed: {
    amountOutMin() {
      if (!this.item2.price) {
        return BigNumber.from(0)
      }
      let price = utils.parseEther(this.item2.price.toString())
      let price1 = utils.parseEther(this.item2.price.toString())
      let progress = utils.parseEther((this.progress).toString())
      let num100 = utils.parseEther('100')
      return price1.sub(price.mul(progress).div(num100))
    }
  },
  mounted() {
    const progress = localStorage.getItem('progress')
    this.progress = progress ? JSON.parse(progress) : 10
    this.isLogin = window.wallet.accounts[0] ? true : false
    this.isLogin && this.loginCallBack()
    window.wallet.on('accountChange', () => {
      this.isLogin = window.wallet.accounts[0] ? true : false
      this.isLogin && this.loginCallBack()
    })
  },
  beforeUnmount () {
    clearInterval(this.timer)
    this.timer = null 
  },
  methods: {
    // 添加代币到钱包
    addTokenHandler() {
      const { symbol, address, logoURI } = this.item2
      window.wallet.addTokenToMeta(address, symbol, logoURI);
    },
    // 记录用户滑点
    progressChange() {
      localStorage.setItem('progress', this.progress)
    },
    // 切换上下币种
    transformHandler(){
      const res = JSON.parse(JSON.stringify(this.item1))
      this.item1 = JSON.parse(JSON.stringify(this.item2))
      this.item2 = res
    },
    maxHandler() {
      this.item1.price = utils.formatEther(this.item1.balance)
      this.price1Change('item1')
    },
    // 交换
    async swapHandler () {
      let promise;
      const payableAmount = utils.parseEther(this.item1.price.toString())
      const amountOutMin = this.amountOutMin
      const to = window.wallet.accounts[0]
      const deadline = utils.parseEther((Math.round(new Date().getTime()/1000) + 15 * 60).toString())

      let limit
      if (window.wallet.connectState == 3) {
        limit = {
          gasPrice: utils.parseUnits(this.gWei.toString(), 9),
          gasLimit: (100000 * this.gWei).toString()
        }
      } else {
        limit = {
          gasPrice: utils.parseUnits(this.gWei.toString(), 9),
        }
      }

      if (this.item1.name == 'BNB') {
        // ETH BNB换其他币
        promise = window.wallet.ContractWETH.swapExactETHForTokensSupportingFeeOnTransferTokens(
          amountOutMin,
          path,
          to,
          deadline,
          {
            value: payableAmount,
            ...limit
          }
        )
      } else if(this.item2.name == 'BNB'){
        // 其他币换ETH BNB
        promise = window.wallet.ContractWETH.swapExactTokensForETHSupportingFeeOnTransferTokens(
          payableAmount,
          amountOutMin,
          path,
          to,
          deadline,
          limit
        )
      } else {
        // 其他币之间互换
        promise = window.wallet.ContractWETH.swapExactTokensForTokensSupportingFeeOnTransferTokens(
          payableAmount,
          amountOutMin,
          path,
          to,
          deadline,
          limit
        )
      }
      
      this.swapLoading = true
      try {
        clearInterval(this.timer)
        const tx = await promise
        await tx.wait()
        ElNotification({
          type: 'success',
          title: 'success',
          message: 'Successful trade'
        })
        this.loginCallBack()
      } catch (error) {
        Tips(error)
      }
      this.swapLoading = false
    },
    // 上面价格变化
    async price1Change(key) {
      let otherKey = key == 'item1' ? 'item2' : 'item1'
      this[key].price = this[key].price.replace(/[^0-9.]/g,'')
      if (this[key].price == '' || this[key].price == '0') {
        this[key].price = '0'
        this[otherKey].price = '0'
        return
      }

      clearInterval(this.timer)
      this.timer = setInterval(() => {
        this.price1Change(key)
      }, 3000)
      
      const price = this[key].price.toString()
      if (price === '0.') {
        return
      }
      let promises = [window.wallet.ContractWETH.getAmountsOut(utils.parseEther(price), [
        this[key].address,
        this[otherKey].address
      ])]
      addressPath = [[this[key].address,this[otherKey].address]]
      this.addresss.forEach(item => {
        if (item.address != this[key].address && item.address != this[otherKey].address) {
          addressPath.push([this[key].address,item.address,this[otherKey].address])
          promises.push(window.wallet.ContractWETH.getAmountsOut(utils.parseEther(price), [
            this[key].address,
            item.address,
            this[otherKey].address
          ]))
        }
      })
      Promise.allSettled(promises).then(data => {
          let result = '';
          let lastIndex;
          data.forEach((item, index) => {
            const {status, value} = item
            if (status == "fulfilled") {
              const last = value[value.length - 1]
              if (!result) {
                result = last
                lastIndex = index
              } else if (last.gt(result)) {
                result = last
                lastIndex = index
              }
            }
          })
          if (result && this[key].price && this[key].price != '0') {
            console.log(lastIndex, '最多可以换'+utils.formatEther(result.toString()))
            this[otherKey].price = utils.formatEther(result)
            path = addressPath[lastIndex]
          }
      })
    },
    async loginCallBack() {
      this.item1.loading = true
      this.item2.loading = true
      this.connectState = window.wallet.connectState
      this.getItemBalance(this.item1.name, this.item1.address, 'item1').then(res => {
        this.item1.balance = res
        this.item1.loading = false
      })
      this.getItemBalance(this.item2.name, this.item2.address, 'item2').then(res => {
        this.item2.balance = res
        this.item2.loading = false
      })
    },
    // 获取第一个币种余额
    async getItemBalance(type, address, keyType) {
      let balance;
      if (type == 'BNB') {
        balance = await window.wallet.provider.getBalance()
      } else {
        const contract = window.wallet.initContract(address)
        if (keyType == 'item1') {
          this.approveLoading = true
          try {
            const data = await contract.allowance(window.wallet.accounts[0], config[config.evn].WETH)
            const res = Math.floor(Number(utils.formatEther(data)))
            if (res <= 0) {
              const tx = await contract.approve(
                config[config.evn].WETH,
                utils.parseEther('10000000000000'),
                {gasPrice: utils.parseUnits('10', 9)}
              )
              await tx.wait();
            }
          } catch (error) {
            Tips(error)
          }
          this.approveLoading = false
        }
        balance = await contract.balanceOf(window.wallet.accounts[0])
      }
      return balance
    },
    // 修改需要转换的币
    async tokenChange(data) {
      if (this.type == 'item1') {
        if (this.item2.address == data.address) {
          this.item2 = JSON.parse(JSON.stringify(this.item1))
        }
      } else {
        if (this.item1.address == data.address) {
          if (this.item1.address == data.address) {
            this.item1 = JSON.parse(JSON.stringify(this.item2))
          }
        }
      }
      data.price = data.price || '0'
      this[this.type] = data
      if (this.isLogin) {        
        this[this.type].loading = true
        this[this.type].balance = await this.getItemBalance(this[this.type].name, this[this.type].address, this.type)
        this[this.type].loading = false
      }
      this.price1Change(this.type)
    },
    copyHandler(text) {
      copy(text)
      ElNotification({
        type: 'success',
        title: 'success',
        message: 'The contract address has been copied to your clipboard'
      })
    },
    toggleGwei(data) {
      this.gWei = data
    },
    openTokensDialog(type) {
      this.type = type
      this.$refs.tokens.open([this.item1.address, this.item2.address,])
    },
    swapSubmit() {
      if (this.professional) {
        this.swapHandler()
      } else {
        this.$refs.swapConfirm.open(this.item1, this.item2)
      }
    },
    // 连接钱包
    loginFn() {
      window.wallet.initNetwork()
    }
  }
}
</script>

<style lang="scss">
.el-checkbox__input.is-checked .el-checkbox__inner{
  background: rgba(252, 190, 47, 1);
  border-color: rgba(252, 190, 47, 1);
}
.el-checkbox__input.is-checked+.el-checkbox__label{
  color: rgba(252, 190, 47, 1);
}
.el-slider__bar{
  background: rgba(252, 190, 47, 1);
}
.el-slider__button{
  border-color: rgba(252, 190, 47, 1);
}
.swap-slippage__select-input{
  width: 40px;
  height: 20px;
  background: none;
  border-radius: 4px;
  border: 1px solid #898989;
  text-align: center;
  color: #fff;
}
.swap-slippage__select-tag{
  height: 20px;
  line-height: 18px;
  margin-right: 10px;
}
  .swap-tabs{
    margin: 0 auto;
    width: 410px;
    .el-tabs__item{
      color: #fff;
    }
    .el-tabs__nav-wrap::after{
      display: none;
    }
    .el-tabs__active-bar{
      background: #fff;
    }
  }
  .swap-info{
    width: 410px;
    border: 1px solid #2A2A2A;
    border-radius: 18px;
    .swap-top{
      border-bottom: 1px solid rgba(42, 42, 42, 1);
      position: relative;
      text-align: center;
      padding: 20px;
      .swap-top__icon{
        position: absolute;
        left: 30px;
        top: 50%;
        transform: translateY(-50%);
        width: 24px;
        height: 19px;
      }
      .swap-info__title{
        font-size: 20px;
        color: #fff;
      }
      .swap-info__title-info{
        font-size: 14px;
        color: rgba(255, 243, 204, 1);
      }
    }
    .swap-item{
      padding: 10px 20px;
    }
    .swap-item__top{
      font-size: 16px;
      font-weight: bold;
      .swap-item__top-icon{
        width: 30px;
        height: 30px;
      }
      .swap-item__top-wallet{
        width: 20px;
        height: auto;
      }
      .swap-item__top-name{
        max-width: 60px;
      }
    }
    .swap-item__input{
      width: 100%;
      height: 74px;
      border: 1px solid #2A2A2A;
      border-radius: 18px;
      background: none;
      font-size: 14px;
      padding: 0 20px;
      color: #fff;

      >input{
        width: 100%;
        height: 100%;
        background: none;
        color: #fff;
        font-size: 16px;
      }
    }
    .swap-item__input.is-disabled{
      background: #666;
      cursor: no-drop;
    }
    .swap-transform{
      width: 40px;
      height: 40px;
      margin: 10px auto;
    }
    .text-yellow{
      font-size: 12px;
      color: rgba(240, 185, 11, 1);
    }
    .text-blue{
      font-size: 16px;
      color: rgba(31, 199, 213, 1);
    }
    .text-16{
      font-size: 16px;
    }
    .text-14{
      font-size: 14px;
    }
    .swap-slippage__select{
      height: 47px;
      background: #1B1B1B;
      border: 1px solid #2A2A2A;
      border-radius: 8px;
      padding: 0 20px;
    }
    .swap-btn{
      margin-right: 30px;
      margin-left: 30px;
      width: calc(100% - 60px);
      height: 60px;
      background: #FCBE2F;
      border: 1px solid #2A2A2A;
      border-radius: 18px;
      color: #fff;
      &.is-disabled{
        background: #e4e7ed;
      }
    }
  }
</style>