<template>
  <el-dialog
    v-model="dialogVisible"
    title="Select a Token"
    width="420px"
    custom-class="tokens"
  >
    <div class="flex-column-start tokens-content">
        <input type="text" class="tokens-input" v-model="searchTxt" placeholder="Search name or paste address">
        <div class="flex-h__1 tokens-list">
            <div class="tokens-list__import flex-row-start align-center PL-30 PR-30 PB-30 PT-30" v-if="importTokens.name">
                <div class="flex-row-start flex-1 align-center">
                    <img v-lazy="'https://assets-cdn.trustwallet.com/blockchains/smartchain/assets/'+importTokens.address+'/logo.png'" class="tokens-list__item-icon MR-10"/>
                    <div class="tokens-list__import-name MR-10">{{importTokens.name}}</div>
                    <div class="tokens-list__import-desc">{{importTokens.symbol}}</div>
                </div>
                <el-button class="tokens-list__import-btn" @click="importHandler">Import</el-button>
            </div>
            <template v-else>
                <div 
                    class="tokens-list__item pointer flex-row-start align-center pointer"
                    v-for="item in getTokens"
                    :key="item.address"
                    @click="selectHandler(item)"
                >
                    <img v-lazy="'https://assets-cdn.trustwallet.com/blockchains/smartchain/assets/'+item.address+'/logo.png'" class="tokens-list__item-icon MR-10"/>
                    <div class="flex-1">
                        <div class="token-list__item-title">{{item.name}}</div>
                        <div class="token-list__item-desc">{{item.symbol}}</div>
                    </div>
                    <div class="token-list__item-price">{{ item.balance ? getBit(utils.formatEther(item.balance), 5) : '' }}</div>
                </div>
            </template>
            
        </div>
    </div>
  </el-dialog>
</template>

<script>
import { utils } from 'ethers'
import { getBit } from '../../../libs/utils'
export default {
    name: 'Tokens',
    data() {
        return {
            utils,
            getBit,
            dialogVisible: false,
            activeAddress: [],
            searchTxt: '',
            getTokens: [],
            // 需要导入的token信息
            importTokens: {},
            tokens: []
        }
        // {
        //     "name": "TDAO",
        //     "symbol": "TDAO",
        //     "address": "0xc4fc54ebc66841443734930c680a69b099a0c15e"
            
        // }
    },
    created() {
        let tokens = localStorage.getItem('tokens')
        this.tokens = tokens ? JSON.parse(tokens) : [{
            "name": "BNB",
            "symbol": "BNB",
            "address": "0xbb4CdB9CBd36B01bD1cBaEBF2De08d9173bc095c"
        },{
            "name": "BUSD",
            "symbol": "BUSD",
            "address": "0xe9e7CEA3DedcA5984780Bafc599bD69ADd087D56"
        },{
            "name": "USDT",
            "symbol": "USDT",
            "address": "0x55d398326f99059fF775485246999027B3197955"
        }]
        this.getTokens = this.tokens
        this.getBalance()
    },
    watch: {
        searchTxt(val) {
            if (!val) {
                this.getTokens = JSON.parse(JSON.stringify(this.tokens))
            }
            const list = this.tokens.filter(item => {
                const { name, address } = item
                if (name.includes(val) || address.includes(val)) {
                    return true
                }
                return false
            })
            this.importTokens = {}
            this.getTokens = JSON.parse(JSON.stringify(list))
            if (list.length <= 0 && val.length === 42) {
                const contract = window.wallet.initContract(val)
                const promise = [contract.name(), contract.symbol()]
                Promise.all(promise).then(([name, symbol]) => {
                    this.importTokens = {
                        name,
                        symbol,
                        address: val
                    }
                })
            }
        }
    },
    methods: {
        // 获取币种余额
        async getBalance() {
            if (!window.wallet.accounts[0]) {
                for (let i = 0; i < this.getTokens.length; i++) {
                    this.getTokens[i].balance = ''
                }
                return
            }
            let promise = []
            for (let i = 0; i < this.getTokens.length; i++) {
                const item = this.getTokens[i];
                let balance
                if (item.address === '0xbb4CdB9CBd36B01bD1cBaEBF2De08d9173bc095c') {
                    balance = window.wallet.provider.getBalance()
                } else {
                    const contract = window.wallet.initContract(item.address)
                    balance = contract.balanceOf(window.wallet.accounts[0])
                }
                promise.push(balance)
            }
            Promise.allSettled(promise).then(data => {
                data.forEach((item, index) => {
                    const { status, value } = item
                    if (status === 'fulfilled') {
                        this.getTokens[index].balance = value
                    } else {
                        this.getTokens[index].balance = ''
                    }
                })
            })
        },

        // 导入新的币
        importHandler() {
            this.tokens.push(this.importTokens)
            this.searchTxt = ''
            localStorage.setItem('tokens', JSON.stringify(this.tokens))
            this.selectHandler(this.importTokens)
        },
        open(address) {
            this.activeAddress = address
            this.dialogVisible = true
            this.getBalance()
        },
        selectHandler(data) {
            this.$emit('change', data)
            this.dialogVisible = false
        }
    }
}
</script>

<style lang="scss">
.tokens-list__import-name{
    font-size: 16px;
    font-weight: bold;
    color: #fff;
}
.tokens-list__import-desc{
    font-size: 14px;
    color: rgb(165, 165, 165);
}
.tokens-list__import-btn,.tokens-list__import-btn:hover{
    background: #FCBE2F;
    border: 1px solid #2A2A2A;
    color: #fff;
}
    .tokens{
        background: rgb(39, 38, 44);
        border-radius: 32px;
        max-height: 100vh;
        display: flex;
        flex-direction: column;

        .el-dialog__close{
            font-size: 20px;
            font-weight: bold;
        }
        .el-dialog__title{
            color: #FFF;
        }
        .el-dialog__header{
            border-bottom: 1px solid #5d5d5d;
        }
        .el-dialog__body{
            flex: 1;
            height: 100%;
            overflow: hidden;
            padding: 0;
        }
        .tokens-content{
            height: 100%;
        }
        .tokens-input{
            background-color: rgb(55, 47, 71);
            border-radius: 16px;
            color: rgb(244, 238, 255);
            display: block;
            font-size: 16px;
            height: 48px;
            outline: 0px;
            padding: 0px 16px;
            width: calc(100% - 60px);
            border: 1px solid rgb(38, 33, 48);
            margin: 20px 30px;
        }
        .tokens-list{
            overflow: auto;
            max-height: 400px;
            &::-webkit-scrollbar {
                width: 8px;
            }
            &::-webkit-scrollbar-thumb {
                background: rgb(184, 173, 210);
                border-radius: 8px;
            }
            &::-webkit-scrollbar-track {
                border-radius: 10px;
            }
            .tokens-list__item{
                padding: 20px 30px;
                transition: all .3s;
                &:hover{
                    background: rgb(8, 6, 11);
                }
                &.is-disabled{
                    background: #333;
                    opacity: .5;
                    cursor: not-allowed;
                }
            }
            .tokens-list__item-icon{
                width: 24px;
                height: 24px;
            }
            .token-list__item-title{
                color: #fff;
                font-size: 16px;
                font-weight: bold;
            }
            .token-list__item-desc{
                color: #999;
            }
            .token-list__item-price{
                font-size: 14px;
                color: #fff;
            }
        }
    }
</style>